import db from '@/services/db';
import { SearchParams } from '@/services/db/web/book';

export function searchBooks(params: SearchParams) {
  if (params.isbn) {
    return db.book.queryRawBooksByIsbn(params.isbn);
  } else {
    return db.book.queryRawBooksByParams(params);
  }
}
