/**
 * @file 搜索表单
 * @author Yangholmes 2025-04-21
 */

import { computed, defineComponent, PropType, ref, toRefs, watch } from 'vue';
import { useI18n } from 'vue-i18n';

import cn from 'classnames';

import SvgIcon from '@/components/svgIcon/SvgIcon';

import { NButton, NForm, NFormItem, NInput, NInputGroup } from 'naive-ui';

import { deepToRaw } from '../vue-utils';

import styles from './searchForm.module.less';
import { SearchParams } from '@/services/db/web/book';

export const initSearchParams: SearchParams = {
  isbn: '',
  title: '',
  authors: '',
  publishers: '',
  // numberOfPages: undefined,
  tags: '',
  rating: undefined,
};

export default defineComponent({
  name: 'SearchForm',
  props: {
    searchParams: {
      type: Object as PropType<SearchParams>,
      default: () => ({ ...initSearchParams })
    },
    isLoading: {
      type: Boolean,
      default: false
    },
    onScan: {
      type: Function as PropType<() => void>
    },
    onChange: {
      type: Function as PropType<(params: SearchParams) => void>
    }
  },
  setup(props) {
    const { searchParams, isLoading } = toRefs(props);

    const { t } = useI18n({ useScope: 'global' });

    const formData = ref<SearchParams>({ ...searchParams.value });
    watch(() => searchParams.value, (newVal) => {
      if (newVal.isbn) {
        formData.value = {
          ...initSearchParams,
          isbn: newVal.isbn
        };
      } else {
        formData.value = {
          ...newVal
        };
      }
    }, { immediate: true });

    function updatFormItemValue<T extends keyof SearchParams>(key: T, value?: SearchParams[T] | null) {
      formData.value[key] = value;
    }

    const isIsbnDisabled = computed(() => {
      return Object.keys(formData.value).filter(k => k !== 'isbn').some(key => {
        return formData.value[key as keyof SearchParams] !== '' && formData.value[key as keyof SearchParams] !== undefined && formData.value[key as keyof SearchParams] !== null;
      });
    });

    const isIsbnNull = computed(() => {
      return formData.value.isbn === '' || formData.value.isbn === undefined || formData.value.isbn === null;
    });

    const onScanIsbn = () => {
      props.onScan?.();
    };

    return () => (<NForm
      class={cn(styles['search-form'])}
      labelPlacement="top"
      model={formData.value}
    >
      <NFormItem label="isbn" path="isbn">
        <NInputGroup>
          <NInput
            disabled={isIsbnDisabled.value}
            placeholder={t('pages.search.searchForm.fields.isbn.placeholder')}
            value={formData.value.isbn}
            onUpdateValue={(v) => {
              updatFormItemValue('isbn', v);
            }}
            onBlur={() => {
              formData.value.isbn = formData.value.isbn?.trim();
            }}
            inputProps={{
              enterKeyHint: 'done'
            }}
          />
          <NButton ghost onClick={onScanIsbn}>
            <SvgIcon name="barcodeScan" />
          </NButton>
        </NInputGroup>
      </NFormItem>
      <NFormItem label={t('pages.search.searchForm.fields.title.label')} path="title">
        <NInput
          disabled={!isIsbnNull.value}
          placeholder={t('pages.search.searchForm.fields.title.placeholder')}
          value={formData.value.title}
          onUpdateValue={(v) => {
            updatFormItemValue('title', v);
          }}
          onBlur={() => {
            formData.value.title = formData.value.title?.trim();
          }}
        />
      </NFormItem>
      <NFormItem label={t('pages.search.searchForm.fields.authors.label')} path="authors">
        <NInput
          disabled={!isIsbnNull.value}
          placeholder={t('pages.search.searchForm.fields.authors.placeholder')}
          value={formData.value.authors}
          onUpdateValue={(v) => {
            updatFormItemValue('authors', v);
          }}
          onBlur={() => {
            formData.value.authors = formData.value.authors?.trim();
          }}
        />
      </NFormItem>
      <NFormItem label={t('pages.search.searchForm.fields.publishers.label')} path="publishers">
        <NInput
          disabled={!isIsbnNull.value}
          placeholder={t('pages.search.searchForm.fields.publishers.placeholder')}
          value={formData.value.publishers}
          onUpdateValue={(v) => {
            updatFormItemValue('publishers', v);
          }}
          onBlur={() => {
            formData.value.publishers = formData.value.publishers?.trim();
          }}
        />
      </NFormItem>
      <NFormItem label={t('pages.search.searchForm.fields.tags.label')} path="tags">
        <NInput
          disabled={!isIsbnNull.value}
          placeholder={t('pages.search.searchForm.fields.tags.placeholder')}
          value={formData.value.tags}
          onUpdateValue={(v) => {
            updatFormItemValue('tags', v);
          }}
          onBlur={() => {
            formData.value.tags = formData.value.tags?.trim();
          }}
        />
      </NFormItem>
      <NFormItem>
        <NButton
          type="primary"
          onClick={() => {
            const params = deepToRaw(formData.value);
            props.onChange?.(params);
          }}
          loading={isLoading.value}
        >
          {t('pages.search.searchForm.actions.search')}
        </NButton>
      </NFormItem>
    </NForm>);
  }
});

