/**
 * @file 搜索页面
 * @author Yangholmes 2025-04-12
 */

import { defineComponent, onMounted, onUnmounted, ref } from 'vue';
import { useI18n, I18nT } from 'vue-i18n';

import cn from 'classnames';

import useTRouter from '@/composite/useTRouter';

import BasicLayout from '../layout/BasicLayout';
import BasicTopBar from '../layout/BasicTopBar';
import DoubleDeckerContainer from '@/components/doubleDeckerContainer/DoubleDeckerContainer';
import SearchForm, { initSearchParams } from './SearchForm';
import SvgIcon from '@/components/svgIcon/SvgIcon';
import Empty from '@/components/empty/Empty';
import BookList from '../cell/BookList';
import BarcodeDecker from '@/components/barcode/BarcodeDecker';

import { NButton, NDrawer } from 'naive-ui';
import { useNaiveFeedback } from '@/composite/useNaiveFeedback';

import { BookInCellRawCover } from '../book/utils';
import { searchBooks } from './utils';
import db from '@/services/db';
import { SearchParams } from '@/services/db/web/book';
import { deepToRaw } from '../vue-utils';

import styles from './search.module.less';

export default defineComponent({
  name: 'Search',
  setup() {
    const { t } = useI18n({ useScope: 'global' });
    const { router } = useTRouter();
    const { dialog } = useNaiveFeedback();

    const getAllBooks = () => {
      return db.book.getAllBooks();
    };
    onMounted(() => {
      try {
        const _params = localStorage.getItem('searchParams') || '{}';
        searchParams.value = JSON.parse(_params);
      } catch (e) {
        console.error(e);
      }
      onSearch();
    });

    const needCache = ref(false);
    onUnmounted(() => {
      needCache.value ? localStorage.setItem('searchParams', JSON.stringify(searchParams.value)) : localStorage.removeItem('searchParams');
    });

    const onNewBarcode = (_isbn: string) => {
      return new Promise<boolean>((resolve) => {
        dialog.success({
          title: t('pages.search.topbar.scanResultDialog.title'),
          content: t('pages.search.topbar.scanResultDialog.content', [_isbn]),
          positiveText: t('common.yes'),
          negativeText: t('common.no'),
          closable: false,
          onPositiveClick: () => {
            searchParams.value = { isbn: _isbn };
            resolve(true);
            layer.value = 1;
          },
          onNegativeClick: () => {
            resolve(false);
          }
        });
      });

    };

    const bookList = ref<BookInCellRawCover[]>([]);
    const renderBookList = () => {
      if (bookList.value.length === 0) {
        return <Empty title={t('pages.search.empty.title')} description={
          <I18nT scope="global" keypath="pages.search.empty.description">
            <SvgIcon name="filter" />
          </I18nT>
        } />;
      }
      return <BookList
        data={bookList.value}
        onPreviewBook={(book) => {
          router.push({
            path: `/book/preview/id/${book.id}`
          });
          needCache.value = true;
        }}
        onEditBook={(book) => {
          router.push({
            path: `/book/edit/id/${book.id}`
          });
          needCache.value = true;
        }}
        onDeleteBook={(book) => {
          db.book.deleteBook(book.id).then(() => {
            bookList.value = bookList.value.filter(b => b.id !== book.id);
          });
        }}
      />;
    };

    const isLoading = ref(false);
    const onSearch = () => {
      const params = deepToRaw(searchParams.value);
      const isNullParams = Object.keys(params).every(key => {
        const val = params[key as keyof SearchParams];
        return val === '' || val === null || val === undefined;
      });
      (isNullParams ? getAllBooks() : searchBooks(params))?.then(res => {
        bookList.value = res;
      });
    };

    const drawerToRef = ref<HTMLElement>();
    const isDrawerShow = ref(false);
    const searchParams = ref(initSearchParams);
    const renderDefault = (go: () => void) => {
      return <BasicLayout
        topBar={
          <BasicTopBar rightSlots={[
            <NButton onClick={() => {
              isDrawerShow.value = true;
            }}>
              <SvgIcon name="filter" />
            </NButton>
          ]}>
            <SvgIcon name="magnifier" /> {t('pages.search.topbar.title')}
          </BasicTopBar>
        }
      >
        <div class={cn(styles.main)} ref={drawerToRef}>
          <div class={cn(styles.list)}>
            {renderBookList()}
          </div>
          <NDrawer
            show={isDrawerShow.value}
            to={drawerToRef.value}
            autoFocus={false}
            onMaskClick={() => {isDrawerShow.value = false;}}
          >
            <SearchForm
              searchParams={searchParams.value}
              isLoading={isLoading.value}
              onScan={go}
              onChange={(params) => {
                searchParams.value = params;
                isDrawerShow.value = false;
                onSearch();
              }}
            />
          </NDrawer>
        </div>
      </BasicLayout>;
    };

    const renderDecker = () => {
      return <BarcodeDecker onBarcodeDetect={onNewBarcode} />;
    };

    const layer = ref<1 | 2>(1);
    return () => <DoubleDeckerContainer
      class={cn(styles.search)}
      layer={layer.value}
      onLayerChange={(l) => {
        layer.value = l;
      }}
    >
      {{
        default: renderDefault,
        decker: renderDecker
      }}
    </DoubleDeckerContainer>;
  }
});

